import React from "react";
import { Link } from 'react-router-dom'
const Header = () => {
  return (
    <div>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/sign-in">Đăng nhập</Link>
        </li>
      </ul>
    </div>
  )
}
export default Header;